import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import React, { useState, useEffect } from 'react';
import { FcContacts, FcBusiness, FcGraduationCap, FcTreeStructure, FcLandscape } from "react-icons/fc";
import PacmanLoader from "react-spinners/PacmanLoader";
import { css } from '@emotion/react';
import About from './components/Panel_About';
import Edu from './components/Panel_Edu';
import Work from './components/Panel_Work';
import Project from './components/Panel_Project';
import Hobbies from './components/Panel_Hobbies';
import "./App.css";

const loader = css`
  display: block;
  margin: 0 auto;
  margin-top: 40vh;
  padding-right: 10rem;
`

function App() {
  const [loading, setLoading] = useState(true);
  const [color, setColor] = useState('var(--light-blue)')

  useEffect(() => {
    setTimeout(() => {
      setLoading(false)
    }, 3000)
  })

  return (
    <Router>
      {loading? <PacmanLoader css={ loader } color={color} size={50}/> : 
      <div className="tabs">
        <div className="panel-list">
          <Switch>
            <Route path="/kaorihata">
              <About/>     
            </Route>
            <Route path="/education">
              <h2 className="panel-title">Education</h2>
              <Edu />
            </Route>
            <Route path="/work">
              <h2 className="panel-title">Work</h2>
              <Work />
            </Route>
            <Route path="/sideprojects">
              <h2 className="panel-title">Side projects</h2>
              <Project/>
            </Route>
            <Route path="/hobbies">
              <h2 className="panel-title">Hobbies</h2>
              <Hobbies/>
            </Route>
          </Switch>         
        </div>
        <ul className="tab-list">
          <li>
            <Link to="/kaorihata" className="tab">
              <FcContacts size={31} />
              <span className="tab-txt">About</span>
            </Link>
          </li>
          <li>
            <Link to="/education" className="tab">
              <FcGraduationCap size={31} />
              <span className="tab-txt">Education</span>
            </Link>
          </li>
          <li>
            <Link to="/work" className="tab">
              <FcBusiness size={31} />
              <span className="tab-txt">Work</span>
            </Link>
          </li>
          <li>
            <Link to="/sideprojects" className="tab">
              <FcTreeStructure size={31} />
              <span className="tab-txt">Side projects</span>
            </Link>
          </li>
          <li>
            <Link to="/hobbies" className="tab">
              <FcLandscape size={31} />
              <span className="tab-txt">Hobbies</span>
            </Link>
          </li>
        </ul>
      </div> }
    </Router>
  );
}

export default App;
